:root {
  --primary-light: #e0f7ff;
  --primary: #6dc3e5;
  --primary-dark: #2980b9;
  --accent: #145c8a;
  --text-dark: #2c3e50;
  --text-light: #ecf0f1;
  --shadow: rgba(0, 0, 0, 0.1);
  --header-height: 85px; /* Increased header height for larger logo */
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
  background: linear-gradient(135deg, var(--primary-light) 0%, #f1fdff 100%);
  color: var(--text-dark);
  line-height: 1.6;
  /* Add padding-top to account for fixed header */
  padding-top: var(--header-height);
}

.App {
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

header {
  background: linear-gradient(to right, var(--primary) 0%, var(--primary-dark) 100%);
  color: white;
  padding: 0;
  box-shadow: 0 4px 6px var(--shadow);
  position: fixed;
  width: 100%;
  z-index: 1000;
  top: 0;
  left: 0;
  height: var(--header-height); /* Fixed header height */
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.logo {
  font-size: 2rem;
  font-weight: 700;
  color: white;
  text-decoration: none;
  display: flex;
  align-items: center;
}

.logo img, .logo svg {
  height: 65px; /* Increased logo size */
  width: auto; /* Maintain aspect ratio */
  margin-right: 10px;
  /* Add a fallback background color while the image loads */
  background-color: rgba(255, 255, 255, 0.1);
  /* Add a subtle transition effect */
  transition: opacity 0.3s ease;
}

/* Create a fallback class to apply when image fails to load */
.logo-fallback {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 65px;
  width: 65px;
  margin-right: 10px;
  background-color: var(--primary-light);
  color: var(--primary-dark);
  font-weight: bold;
  border-radius: 8px;
}

nav ul {
  display: flex;
  list-style: none;
}

nav ul li {
  margin-left: 2rem;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-weight: 500;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  transition: all 0.3s ease;
}

nav ul li a:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

/* Mobile menu button */
.menu-toggle {
  display: none;
  cursor: pointer;
  font-size: 1.8rem; /* Larger icon */
  color: white;
  padding: 10px; /* Larger touch target */
  -webkit-tap-highlight-color: transparent; /* Remove highlight on tap for iOS */
}

.hero {
  background: linear-gradient(135deg, var(--primary-dark) 0%, var(--primary) 100%);
  min-height: calc(100vh - var(--header-height)); /* Account for header */
  display: flex;
  align-items: center;
  color: white;
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  width: 150%;
  height: 150%;
  background: radial-gradient(ellipse at center, rgba(255,255,255,0.2) 0%, rgba(255,255,255,0) 70%);
  top: -25%;
  left: -25%;
  animation: pulse 15s infinite;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes pulse {
  0% { transform: scale(1); opacity: 0.5; }
  50% { transform: scale(1.05); opacity: 0.8; }
  100% { transform: scale(1); opacity: 0.5; }
}

.hero-content {
  position: relative;
  z-index: 10;
  max-width: 700px;
  padding: 4rem 0; /* Add padding to prevent content being cut off */
}

.hero h1 {
  font-size: 3.5rem;
  margin-bottom: 1.5rem;
  line-height: 1.2;
}

.hero p {
  font-size: 1.25rem;
  margin-bottom: 2rem;
  opacity: 0.9;
}

.cta-button {
  display: inline-block;
  background-color: white;
  color: var(--primary-dark);
  padding: 0.8rem 2rem;
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.cta-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.section {
  padding: 5rem 0;
}

.section-title {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: var(--primary-dark);
}

.section-subtitle {
  text-align: center;
  font-size: 1.2rem;
  max-width: 700px;
  margin: 0 auto 3rem;
  color: var(--text-dark);
  opacity: 0.8;
}

.features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.feature-card {
  background: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}

.feature-icon {
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
  color: white;
  font-size: 2.5rem;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.feature-content {
  padding: 1.5rem;
}

.feature-content h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: var(--primary-dark);
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: center;
}

.about-image {
  background: linear-gradient(135deg, var(--primary-light) 0%, var(--primary) 100%);
  height: 400px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.about-image img, .about-image svg {
  max-width: 80%;
  max-height: 80%;
  width: 320px;
  height: 320px;
}

.about-text h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  color: var(--primary-dark);
}

.contact {
  background: linear-gradient(135deg, var(--primary-light) 0%, white 100%);
}

.contact-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
}

.contact-info h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  color: var(--primary-dark);
}

.contact-method {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
}

.contact-icon {
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
  color: white;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1rem;
  font-size: 1.2rem;
}

.contact-form {
  background: white;
  padding: 2.5rem;
  border-radius: 8px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: var(--text-dark);
}

.form-control {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.form-control:focus {
  outline: none;
  border-color: var(--primary);
}

textarea.form-control {
  resize: vertical;
  min-height: 120px;
}

.submit-btn {
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
  color: white;
  border: none;
  padding: 0.8rem 2rem;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
}

.submit-btn:hover {
  background: linear-gradient(135deg, var(--primary-dark) 0%, var(--accent) 100%);
  transform: translateY(-3px);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}

footer {
  background: var(--primary-dark);
  color: white;
  padding: 3rem 0;
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
}

.footer-column h3 {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
  position: relative;
  padding-bottom: 0.5rem;
}

.footer-column h3::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 40px;
  height: 2px;
  background: var(--primary);
}

.footer-column ul {
  list-style: none;
}

.footer-column ul li {
  margin-bottom: 0.8rem;
}

.footer-column ul li a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-column ul li a:hover {
  color: white;
}

.social-icons {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.social-icon {
  background: rgba(255, 255, 255, 0.1);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.social-icon:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-3px);
}

.footer-bottom {
  margin-top: 3rem;
  text-align: center;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

@media (max-width: 992px) {
  :root {
    --header-height: 70px; /* Smaller header on tablets */
  }
  
  .hero h1 {
    font-size: 3rem;
  }
  
  .about-content, .contact-container {
    grid-template-columns: 1fr;
  }
  
  .about-image {
    height: 300px;
  }
}

@media (max-width: 768px) {
  :root {
    --header-height: 70px; /* Increased header height on mobile */
  }
  
  .header-content {
    padding: 0 1rem;
  }

  .logo img, .logo svg {
    height: 50px; /* Increased logo size on mobile but still responsive */
  }
  
  .menu-toggle {
    display: block;
  }

  nav {
    position: fixed;
    top: var(--header-height);
    left: 0;
    width: 100%;
    background: var(--primary-dark);
    padding: 1rem 0;
    box-shadow: 0 4px 6px var(--shadow);
    transform: translateY(-100%);
    opacity: 0;
    transition: all 0.3s ease;
    z-index: 999;
  }
  
  nav.active {
    transform: translateY(0);
    opacity: 1;
  }
  
  nav ul {
    flex-direction: column;
    align-items: center;
  }
  
  nav ul li {
    margin: 0.5rem 0;
    width: 100%;
    text-align: center;
  }
  
  nav ul li a {
    display: block;
    padding: 0.8rem;
  }
  
  .hero h1 {
    font-size: 2.2rem;
  }
  
  .hero p {
    font-size: 1.1rem;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .about-text h2, .contact-info h2 {
    font-size: 2rem;
  }
  
  .contact-container {
    gap: 2rem;
  }
  
  .contact-form {
    padding: 1.5rem;
  }
}

/* Fix for iPhone notch */
@supports (padding: max(0px)) {
  header {
    padding-left: max(2rem, env(safe-area-inset-left));
    padding-right: max(2rem, env(safe-area-inset-right));
    padding-top: max(0px, env(safe-area-inset-top));
  }
  
  .container {
    padding-left: max(2rem, env(safe-area-inset-left));
    padding-right: max(2rem, env(safe-area-inset-right));
  }
}
